.App {
    max-width: 1300px;
    width: 90%;
    margin: 0 auto;
    height: 100%;
    margin: 0 auto;
    margin-top: 100px;
}

.App button {
    visibility: hidden;
    margin-top: 150px;
}

.memeGrid {
    columns: 3;
    column-fill: balance;
    column-gap: 20px;
    margin-bottom: 100px;
    position: relative;
}

@media screen and (max-width: 825px) {
    .memeGrid {
        columns: 2;
    }
}

@media screen and (max-width: 550px) {
    .memeGrid {
        columns: 1;
    }
}
