@import url(https://fonts.googleapis.com/css?family=Roboto:300,500&display=swap);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

a,
a:active {
    text-decoration: none;
    color: inherit;
}

html,
body {
    min-height: 100%;
    width: 100%;
    background: #14062f;
}

.App {
    max-width: 1300px;
    width: 90%;
    margin: 0 auto;
    height: 100%;
    margin: 0 auto;
    margin-top: 100px;
}

.App button {
    visibility: hidden;
    margin-top: 150px;
}

.memeGrid {
    -webkit-columns: 3;
            columns: 3;
    -webkit-column-fill: balance;
            column-fill: balance;
    grid-column-gap: 20px;
    -webkit-column-gap: 20px;
            column-gap: 20px;
    margin-bottom: 100px;
    position: relative;
}

@media screen and (max-width: 825px) {
    .memeGrid {
        -webkit-columns: 2;
                columns: 2;
    }
}

@media screen and (max-width: 550px) {
    .memeGrid {
        -webkit-columns: 1;
                columns: 1;
    }
}

.captionContainer {
    padding: 17px 22px;
    display: flex;
    justify-content: space-between;
}

.captionInfo p:first-child {
    font-weight: 500;
}
.memeCard {
    margin-bottom: 20px;
    overflow: hidden;
    border-radius: 10px;
}

.cardWrapper {
    position: relative;
}

.meme_imgwrapper > img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
}

.caption {
    max-height: 0;
    position: absolute;
    bottom: 0;
    overflow: hidden;
    color: white;
    width: 100%;
    background: -webkit-gradient(linear, left top, left bottom, from(#302858), color-stop(191.18%, #490074));
    background: linear-gradient(180deg, #302858 0%, #490074 191.18%);
    -webkit-transition: max-height ease 0.4s;
    transition: max-height ease 0.4s;
    font-family: "Roboto";
    font-weight: 300;
    font-size: 14px;
    line-height: 19px;
}

.captionSave {
    display: flex;
    align-self: center;
    justify-content: flex-end;
    align-items: center;
    flex-basis: 30%;
    color: #ffcb15;
}

.captionSave svg {
    margin-right: 7px;
}

.cardWrapper:hover > .caption {
    max-height: 200px;
}

.overlay {
    position: fixed;
    height: 100%;
    width: 100%;
    background: rgba(12, 4, 29, 0.8);
    top: 0;
    left: 0;
    z-index: 2;
}

@media screen and (max-width: 825px) {
    .caption {
        max-height: none;
        max-height: initial;
        position: static;
        position: initial;
    }
}

.memeOverlay {
    position: fixed;
    top: 50%;
    left: 50%;
    width: 80%;
    z-index: 99;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    max-width: 38vh;
}

.SavedMeme {
    max-width: 1300px;
    width: 90%;
    margin: 0 auto;
    height: 100%;
    margin: 0 auto;
    margin-top: 100px;
}

.memeGrid {
    -webkit-columns: 3;
            columns: 3;
    grid-column-gap: 20px;
    -webkit-column-gap: 20px;
            column-gap: 20px;
    height: 200%;
    position: relative;
}

.saved_msg {
    font-family: "Roboto";
    color: white;
    margin-bottom: 33px;
}

.saved_msg h1 {
    font-style: normal;
    font-weight: bold;
    text-align: center;
    font-size: 22px;
}

.saved_msg p {
    font-weight: 300;
    text-align: center;
    font-size: 19px;
}
@media screen and (max-width: 825px) {
    .memeGrid {
        -webkit-columns: 2;
                columns: 2;
    }
}

@media screen and (max-width: 550px) {
    .memeGrid {
        -webkit-columns: 1;
                columns: 1;
    }
}

.Nav {
    background: #302858;
    width: 100%;
    font-family: "Roboto";
    padding: 15px 0;
    position: fixed;
    z-index: 2;
    top: 0;
}

.nav_logo {
    font-size: 24px;
    flex-basis: 25%;
    background: -webkit-gradient(linear, left top, right top, color-stop(-2.41%, #ec006a), color-stop(98.62%, #992f8b));
    background: linear-gradient(90deg, #ec006a -2.41%, #992f8b 98.62%);
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-text-fill-color: transparent;
    display: flex;
    justify-content: space-between;
}

.nav_search {
    background: white;
    flex-basis: 60%;
    border-radius: 6px;
}

.nav_search input {
    width: 100%;
    border: none;
    padding: 9px 13px;
    border-radius: 6px;
    font-size: 15px;
}

.nav_container {
    max-width: 1300px;
    width: 90%;
    margin: 0 auto;
    display: flex;
    align-items: center;
}

.nav_nav {
    flex: 1 1;
}

.nav_nav ul {
    display: flex;
    list-style: none;
    justify-content: flex-end;
}

.nav_nav ul li a {
    color: #ffffff;
    opacity: 0.6;
    text-decoration: none;
}

.nav_nav ul li:not(:last-child) {
    margin-right: 40px;
}

.nav_content {
    flex: 1 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.nav_toggle {
    display: none;
}

@media screen and (max-width: 825px) {
    .nav_container {
        flex-direction: column;
        align-items: initial;
    }

    .nav_toggle {
        display: block;
    }

    .nav_content {
        flex-direction: column;
        overflow: hidden;
        align-items: initial;
        width: 90%;
        margin-left: auto;
        margin-right: auto;
        -webkit-transition: all ease 0.5s;
        transition: all ease 0.5s;
    }

    .nav_search {
        margin-top: 30px;
    }

    .nav_nav ul {
        flex-direction: column;
        margin-top: 10px;
    }

    .nav_nav ul li {
        margin: 20px 0;
    }
}

